<template>
	<div class="section single-page">
		<div class="content">
			<div class="max-w-md mx-auto">
				<img :src="randomExpression()" class="h-32 mb-10 w-auto mx-auto" alt="Sorry!" />
				<h2 class="text-center font-light text-brand mb-2 tracking-tight">{{ semanticMessage }}</h2>
				<p class="text-center text-lg tracking-tight">
					{{ errorMessage }}
				</p>
			</div>
		</div>
	</div>
</template>

<script>
import { mapActions } from "vuex";

export default {
	props: ["text", "error"],
	data() {
		return {
			semanticMessage: "Oh no!",
			errorMessage: "This page is under construction. Check back soon!"
		};
	},
	methods: mapActions(["doneLoading"]),
	beforeMount() {
		if (this.text) this.semanticMessage = this.text;
		if (this.error) this.errorMessage = this.error;
	},
	mounted() {
		this.doneLoading();
	},
	methods: {
		...mapActions(["doneLoading"]),
		randomExpression() {
			const files = [
				"images/error/expressionless.svg",
				"images/error/flushed.svg",
				"images/error/rolling-eyes.svg",
				"images/error/sweat.svg",
				"images/error/thinking.svg"
			];
			const randomNumber = Math.floor(Math.random() * Math.floor(5));

			return files[randomNumber];
		}
	}
};
</script>
